/* eslint-disable semi */
import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import Home from '../components/control/Home.vue'
import Content from '../components/control/Content.vue'
// import Notice from '../components/control/Notice.vue'
const Notice =() => import( '../components/control/Notice.vue')

// import Noticeform from '../components/control/Noticeform.vue'
const Noticeform =() => import( '../components/control/Noticeform.vue')

import Image from '../components/control/Image.vue'
import Help from '../components/control/Help.vue'
// import ARS from '../components/control/C-ARS.vue'
const ARS =() => import( '../components/control/C-ARS.vue')


// import Show from '../components/Show.vue'
const Show =() => import( '../components/Show.vue')
// import Test from '../components/websocket.vue'
const Test =() => import( '../components/websocket.vue')

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/Show' },
  { path: '/login', component: Login },
  { path: '/home', component: Home },
  {
    path: '/content',
    component: Content,
    children: [
      { path: '/chat', component: ARS },
      { path: '/notice', component: Notice },
      { path: '/noticeAdd', component: Noticeform },
      { path: '/noticeUpdate/:id', component: Noticeform },
      { path: '/image', component: Image },
      { path: '/help', component: Help },
      
    ]
  },
  // 显示端
  { path: '/show', component:Show},
  { path: '/test', component: Test }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // to 将要访问的路径
  // from 代表从哪个路径跳转而来
  // next 是一个函数，表示放行
  // next() 放行     next('/login') 强制跳转到
  if (to.path === '/login') next();
  // 获取token
  const userId = window.sessionStorage.getItem('userId')
  if (!userId) return next('/login')
  next()
})

export default router
