<template>
  <div>
    <main>
      <form class="form">
        <div class="form__cover"></div>
        <div class="form__loader">
          <div class="spinner active">
            <svg class="spinner__circular" viewBox="25 25 50 50">
              <circle
                class="spinner__path"
                cx="50"
                cy="50"
                r="20"
                fill="none"
                stroke-width="4"
                stroke-miterlimit="10"
              />
            </svg>
          </div>
        </div>
        <div class="form__content">
          <h1>登录/注册</h1>
          <div class="styled-input">
            <input type="text" class="styled-input__input" v-model="form.username" />
            <div class="styled-input__placeholder">
              <span class="styled-input__placeholder-text">Username</span>
            </div>
            <div class="styled-input__circle"></div>
          </div>
          <div class="styled-input">
            <input type="password" class="styled-input__input" v-model="form.password" />
            <div class="styled-input__placeholder">
              <span class="styled-input__placeholder-text">Password</span>
            </div>
            <div class="styled-input__circle"></div>
          </div>
          <div class="radio">
            <van-radio-group v-model="isShow" direction="horizontal">
              <van-radio name="false" checked-color="#FFB600">控制端</van-radio>
              <van-radio name="true" checked-color="#FFB600">显示端</van-radio>
            </van-radio-group>
          </div>
          <div class="buttonContainer">
            <button type="button" class="styled-button" @click="login()">
              <span class="styled-button__real-text-holder">
                <span class="styled-button__real-text">Login</span>
                <span class="styled-button__moving-block face">
                  <span class="styled-button__text-holder">
                    <span class="styled-button__text">Login</span>
                  </span>
                </span>
                <span class="styled-button__moving-block back">
                  <span class="styled-button__text-holder">
                    <span class="styled-button__text">Login</span>
                  </span>
                </span>
              </span>
            </button>
            <button type="button" class="styled-button" @click="register()">
              <span class="styled-button__real-text-holder">
                <span class="styled-button__real-text">Register</span>
                <span class="styled-button__moving-block face">
                  <span class="styled-button__text-holder">
                    <span class="styled-button__text">Register</span>
                  </span>
                </span>
                <span class="styled-button__moving-block back">
                  <span class="styled-button__text-holder">
                    <span class="styled-button__text">Register</span>
                  </span>
                </span>
              </span>
            </button>
          </div>
        </div>
      </form>
    </main>
  </div>
</template>

<script>


export default {
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      isShow: 'false',
    }
  },
  mounted () {
    // 'use strict';

    var placeholders = document.querySelectorAll('.styled-input__placeholder-text'),
      inputs = document.querySelectorAll('.styled-input__input');

    placeholders.forEach(function (el) {
      var value = el.innerText,
        html = '';

      for (var _isArray = Array.isArray(value), _iterator = _isArray ? value : value[Symbol.iterator](), _i = 0; ;) {
        var _ref;

        if (_isArray) {
          if (_i >= _iterator.length) break;
          _ref = _iterator[_i++];
        } else {
          _i = _iterator.next();
          if (_i.done) break;
          _ref = _i.value;
        }

        var w = _ref;

        if (!value) value = '&nbsp;';
        html += '<span class="letter">' + w + '</span>';
      }
      el.innerHTML = html;
    });

    inputs.forEach(function (el) {
      var parent = el.parentNode;
      el.addEventListener('focus', function () {
        parent.classList.add('filled');
        placeholderAnimationIn(parent, true);
      }, false);
      el.addEventListener('blur', function () {
        if (el.value.length) return;
        parent.classList.remove('filled');
        placeholderAnimationIn(parent, false);
      }, false);
    });

    function placeholderAnimationIn (parent, action) {
      var act = action ? 'add' : 'remove';
      var letters = parent.querySelectorAll('.letter');
      letters = [].slice.call(letters, 0);
      if (!action) letters = letters.reverse();
      letters.forEach(function (el, i) {
        setTimeout(function () {
          var contains = parent.classList.contains('filled');
          if (action && !contains || !action && contains) return;
          el.classList[act]('active');
        }, 50 * i);
      });
    }

    setTimeout(function () {
      document.body.classList.add('on-start');
    }, 100);

    setTimeout(function () {
      document.body.classList.add('document-loaded');
    }, 1800);
  },
  methods: {
    login: async function () {
      var rules = /\w{3,10}/;
      var msg = ''
      if (this.form.username === '') {
        msg += '用户名不能为空\n'
      } else if (!rules.test(this.form.username)) {
        msg += '用户名应为3到10位字母或数字\n'
      }
      if (this.form.password === '') {
        msg += '密码不能为空\n'
      } else if (!rules.test(this.form.password)) {
        msg += '密码应为3到10位字母或数字\n'
      }

      if (msg) return this.$toast.fail(msg);
      // 请求登录接口
      const { data: res } = await this.$http.post('/my/user/login' , this.form)
      if (res.code !== 0) return this.$toast.fail('登录失败,'+res.msg)
        this.$toast.success('登录成功')
      window.sessionStorage.setItem('userId', res.data.id)
      if (this.isShow === 'true') { this.$router.push('/show') }
      else { this.$router.push('/home') }
    },
    register:async function () {
      var rules = /\w{3,10}/;
      var msg = ''
      if (this.form.username === '') {
        msg += '用户名不能为空\n'
      } else if (!rules.test(this.form.username)) {
        msg += '用户名应为3到10位字母或数字\n'
      }
      if (this.form.password === '') {
        msg += '密码不能为空\n'
      } else if (!rules.test(this.form.password)) {
        msg += '密码应为3到10位字母或数字\n'
      }
      if (msg) return this.$toast.fail(msg);

      //  请求注册接口
      const { data: res } = await this.$http.post('/my/user/register' , this.form)
      if (res.code !== 0 ) return this.$toast.fail('注册失败,'+res.msg)
      this.$toast.success('注册成功!\n请登录')
      this.form.username = '';
      this.form.password = '';
    }
  },
}

</script>

